.video-jacker.nft-jacker.drop.future {
    overflow: hidden;
    
    .cover-image {
        display:  none;
    }

    canvas {
        width: 100% !important;
        height: 100% !important;
        top: 0px;
        left: 0px;
    }

    .song-waveform {
        z-index: 7;
    }

    .drop-description {
        z-index: 3;

        p {
            margin-bottom: 20px;
        }

        padding-top: 40px;
        text-align: center;
        background: linear-gradient(90deg, #0d0d0dd6, #000000bf), url(https://grainy-gradients.vercel.app/noise.svg) !important;
        height: -moz-fit-content;
        height: fit-content;
        font-weight: bolder;
        font-size: 25px;
        color: white;
    }

    .stems-unlocked {
        display: none;
    }

    video {
        /*
        position: absolute;
        width: 80%;
        //height: 80%;
        z-index: 6;
        margin: auto;
        left: 0px;
        bottom: 0px;
        top: 0px;
        right: 0px;
        */
        box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    }

    .spore-embed-helper {
//        z-index: 7;
        top: unset;
        bottom: 20px;
        font-size: 16pt !important;
    }
    
    .spore-embed-container {
        min-height: 98vh;
        padding: 0px !important;
        padding-top: 30vh !important;
        background: unset !important;
        background-color: unset !important;
        //position: absolute;

        .shuffle-buttons {
            top: 80vh;

            .play-and-slider {
                z-index: 8;
            }
        }
   }

    iframe {
        border: none !important;
        box-shadow: none !important;
        background-color: unset;
        transform: translate(0, -10%);
    }
}
